
let devMode = false;
let editingProject = null;

function showNotification(message) {
  const notification = document.getElementById('notification');
  notification.textContent = message;
  notification.style.display = 'block';
  setTimeout(() => {
    notification.style.display = 'none';
    notification.textContent = '';
  }, 3000);
}

function toggleDevMode() {
  devMode = !devMode;
  document.getElementById('addProjectBtn').style.display = devMode ? 'block' : 'none';
  document.querySelectorAll('.project').forEach(p => p.classList.toggle('dev-active', devMode));
}

function openProjectModal(isEdit = false) {
  document.getElementById('projectModal').style.display = 'flex';
  document.getElementById('modalTitle').textContent = isEdit ? 'Edit Project' : 'Add Project';
}

function closeProjectModal() {
  document.getElementById('projectModal').style.display = 'none';
  document.getElementById('modalProjectName').value = '';
  document.getElementById('modalProjectLink').value = '';
  editingProject = null;
}

function confirmAddProject() {
  const name = document.getElementById('modalProjectName').value.trim();
  const link = document.getElementById('modalProjectLink').value.trim();
  if (!name || !link) {
    showNotification('Please enter both name and link.');
    return;
  }
  if (editingProject) {
    const anchor = editingProject.querySelector('a');
    anchor.textContent = name;
    anchor.href = link;
  } else {
    const projectList = document.getElementById('projectList');
    const div = document.createElement('div');
    div.className = 'project';
    if (devMode) div.classList.add('dev-active');
    div.innerHTML = `<a href="${link}" target="_blank">${name}</a>
      <div class="project-actions">
        <button onclick="editProject(this)">Edit</button>
        <button onclick="deleteProject(this)">Delete</button>
      </div>`;
    projectList.appendChild(div);
  }
  closeProjectModal();
}

function deleteProject(button) {
  const project = button.closest('.project');
  project.remove();
}

function editProject(button) {
  editingProject = button.closest('.project');
  const anchor = editingProject.querySelector('a');
  document.getElementById('modalProjectName').value = anchor.textContent;
  document.getElementById('modalProjectLink').value = anchor.href;
  openProjectModal(true);
}

function handleFormSubmit(event) {
  event.preventDefault();
  const name = document.getElementById('name').value.trim();
  const email = document.getElementById('email').value.trim();
  const message = document.getElementById('message').value.trim();
  if (!name || !email || !message) {
    showNotification('Please fill out all fields.');
    return;
  }
  showNotification('Message sent successfully!');
  document.getElementById('name').value = '';
  document.getElementById('email').value = '';
  document.getElementById('message').value = '';
}
